.about {
  padding-top: 76px;
  padding-bottom: 92px;

  @include tablet {
    padding-top: 25px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 57px;
    padding-bottom: 66px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;

    @include tablet {
      margin-bottom: 25px;
    }

    @include mobile {
      margin-bottom: 24px;
    }
  }

  &__link {
    width: 180px;
  }

  &__content {
    width: 84%;

    @include desktop {
      width: 80%;
    }

    @include tablet {
      width: 74%;
      font-size: 178px;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    @include mobile {
      margin-bottom: 39px;
    }
  }

  &__text-wrapper {
    margin-bottom: 40px;

    @include tablet {
      height: 200px;
      margin-bottom: 37px;

      overflow: hidden;
    }

    @include mobile {
      height: 329px;
      margin-bottom: 48px;
    }
  }

  &__img {
    margin-left: -22px;
    margin-top: 3px;

    @include tablet {
      width: 192px;
      height: 362px;

      margin-top: 0;
    }
  }
}
