.logo-link {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 201px;
  padding-left: 11px;

  @include tablet {
    padding-left: 0;
  }

  &--long {
    width: 216px;
  }

  &__title {
    margin: 0;

    font-family: $font-main;
    font-size: 25px;
    font-weight: bold;

    color: $color-text-attention;

    @include mobile {
      margin-bottom: 3px;
    }

    &--secondary {
      color: $color-text-dark;
    }
  }

  &__text {
    margin: 0;

    font-family: $font-secondary;
    font-size: 12px;
    font-weight: 500;

    color: $color-text-secondary;
  }

  &__img {
    @include mobile {
      width: 38px;
      height: 38px;
    }

    &--big {
      @include mobile {
        width: 52px;
        height: 52px;
      }
    }
  }
}
